<template>
    <div class="product box box-success">
        <div class="box-header with-border">
            <h3 class="box-title">Đang bán</h3>
            <div class="box-tools pull-right">
                <button type="button" class="btn btn-box-tool" data-widget="collapse"><i class="fa fa-minus"></i>
                </button>
            </div>
        </div>
        <div class="box-body no-padding" id="store-product">
            <div class="row">
                <div v-for="item in product" :key="item.id" class="col-lg-3 col-md-3 col-sm-4 col-xs-6">
                    <router-link :to="{name: 'route.product', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}" v-bind:title="item.name">
                        <div class="user-product item">
                            <img class="img" :src="api+'/image/product/thumbnail/'+item.image">
                            <h2 class="name">{{ item.name }}</h2>
                            <span v-if="item.price != 0" class="price">{{ number_format(item.price,0) }} đ</span>
                            <span v-else class="price">Thoả thuận</span>
                        </div>
                    </router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from "axios";

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            product:[]
        }
    },
    props:['url_store'],
    methods:{
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        }
    },
    created(){
        axios.get(this.api + '/public/store/product',{
            params:{
                url_store: this.url_store
            }
        })
        .then(response=>{
            this.product = response.data;
        });
    }
}
</script>

