<template>
    <div class="col-lg-12 col-md-12">
        <ContentHeader :store="store" />
        <AboutStore :store="store" :count="count" />
        <Product :url_store="this.$route.params.store" />
    </div>   
</template>

<script>
import ContentHeader from "./ContentHeader";
import AboutStore from "./AboutStore";
import Product from "./Product";

import axios from "axios";

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            store:{},
            count:[]
        }
    },
    components: {ContentHeader, AboutStore, Product},
    created(){
        axios.get(this.api + "/public/store/" + this.$route.params.store)
        .then(response=>{
            this.store = response.data.store;
            this.count = response.data.count;
        });
    }
}
</script>