<template>
    <div class="detail-content">
        <div class="row">
            <div class="col-lg-7 col-md-6">
                <div class="user box box-widget widget-user-2">
                    <div class="widget-user-header">
                        <div class="widget-user-image">
                            <img class="img-circle store-avata" v-bind:src="store.avata ? api+'/image/avata/'+store.avata : '/tizi/img/avata-default.jpg'" :title="store.name_store" :alt="store.name_store">
                        </div>
                        <h1 class="widget-user-username"><i :class="{'fa fa-circle': true, 'text-success': online, 'text-danger': !online}"></i> {{ store.name_store }}</h1>
                        <h5 class="widget-user-desc">
                            <a v-if="store.id_group==3 || store.id_group==2">
                                <i class="fa fa-archive" aria-hidden="true"></i> Cửa hàng
                            </a>
                            <a v-if="store.id_group==1">
                                <i class="fa fa-user" aria-hidden="true"></i> Cá nhân
                            </a>
                            <a v-if="store.username != $store.state.user.username" v-on:click="chat(store)">
                                <i class="fa fa-envelope" aria-hidden="true"></i> Trò chuyện
                            </a>
                        </h5>
                    </div>
                    <div class="box-footer no-padding">
                        <ul class="nav nav-stacked">
                            <li>
                                <a>
                                    <i class="fa fa-calendar"></i> Ngày tham gia:<span class="pull-right badge bg-blue">{{ getTime(store.created_at).format('L') }}</span>
                                </a>
                            <li><a><i class="fa fa-user"></i> Username <span class="pull-right badge bg-blue">{{ store.username }}</span></a>
                            </li>
                            <li><a><i class="fa fa-shopping-cart"></i> Đang bán <span class="pull-right badge bg-aqua">{{ getCountProduct(count, 1) }}</span></a>
                            </li>
                            <li>
                                <a><i class="fa fa-cart-arrow-down"></i> Đã bán <span class="pull-right badge bg-green">{{ getCountProduct(count, 2) }}</span></a>
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
            <div :class="{'col-lg-5 col-md-6':true, aboutmobile: true , showabout: isAbout, hiddenabout: !isAbout}">
                <ul class="nav nav-stacked">
                    <li>
                        <a href="#">
                            <i class="fa fa-user"></i> Họ Tên:
                            <span class="pull-right badge bg-blue">
                                {{ store.fullname }}
                            </span>
                        </a>
                    </li>
                    <li><a href="#"><i class="fa fa-phone"></i> Di dộng:<span class="pull-right badge bg-blue">{{ store.phone }}</span></a>
                    </li>
                    <li><a href="#"><i class="fa fa-map-marker"></i> Địa chỉ:<span class="pull-right badge bg-blue">{{ store.address }}</span></a>
                    </li>
                    <li>
                        <a target="_blank" :href="store.facebook"><i class="fa fa-link"></i> Facebook:
                            <span class="pull-right badge bg-blue">{{ store.facebook }}</span>
                        </a>
                    </li>
                    <li>
                        <a>
                            <i class="fa fa-link"></i> Zalo:
                            <span class="pull-right badge bg-blue">{{ store.zalo }}</span>
                        </a>
                    </li>
                    
                </ul>
                <div class="box-menu">
                    <a v-on:click="isAbout = !isAbout" class="about-menu-icon btn btn-dropbox "><i class="fa fa-user"></i></a>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import moment from "moment";
import axios from "axios";
import { mapGetters } from 'vuex';

export default {
    data(){
        return {
            api: this.$store.state.domainApi,
            isAbout: false
        }
    },
    methods:{
        getCountProduct(data, status){
            var item = data.find(e=>e.status==status);
            return item ? item.count_product : 0;
        },
        getTime(date){
            var time = new moment(date);
            return time;
        },
        chat(item){
            if(this.$store.state.token){
                this.openBoxMessage(item);
            }else this.$swal.fire({
                title: 'Vui lòng đăng nhập !',
                icon: 'warning',
                showCancelButton: true,
                confirmButtonColor: '#3085d6',
                cancelButtonColor: '#d33',
                cancelButtonText: 'Thoát',
                confirmButtonText: 'Đăng nhập'
            }).then((result) => {
                this.$store.commit('getRoute', this.$route);// lich trang truoc khi login
                if (result.value) this.$router.push({name: 'login'});
            });
        },
        openBoxMessage(item){
            var data={
                id_product: 0,
                id_user: item.id,
                username: item.username,
                avata: item.avata
            };
            this.$store.state.boxMessage.data = data;

            axios.get(this.api+"/auth/message",{
                params:{
                    id_product:data.id_product,
                    id_user1: data.id_user,
                    id_user2: this.$store.state.user.id 
                }
            })
            .then(response=>{
                this.$store.state.boxMessage.content = response.data;
                this.$store.state.boxMessage.isBoxMessage = true;
            });

            if(this.$store.state.user.id != item.id_user1 && item.status==0)
            this.viewMessage(item.id_product, item.id_user1);
        },
        viewMessage(id_product, id_user1){
            axios.get(this.api+"/auth/message/view-message",{
                headers:{ Authorization: `Bearer ${this.$store.state.token}` },
                params:{ id_product: id_product, id_user1: id_user1}
            })
            .then(response=>{
                if(response.data.success)
                this.$store.state.countNewMessage--;
            });
        },
        check_online(){
            console.log(this.store);
            this.socket.emit("check_online", {id: this.store.id});
        }
    },
    props:['store','count'],
    created(){
        moment.locale("vi");
    },
    computed: {
        ...mapGetters(['socket','online'])
    },
    updated(){
        this.check_online();
    }
}
</script>

